.stocks {
  min-height: 796px;
  padding-top: 116px;

  @media (max-width: $tablet-width) {
    min-height: 978px;
    padding-top: 93px;
  }

  @media (max-width: $mobile-width) {
    min-height: 1290px;
    padding-top: 69px;
  }
}

.stocks h2 {
  font-family: $textBebasCyrillic;
  font-size: 49px;
  line-height: 49px;
  font-weight: 400;
  letter-spacing: 0.03em;
  color: #102654;
  text-align: center;
  padding-bottom: 54px;

  @media (max-width: $tablet-width) {
    font-size: 42px;
    line-height: 42px;
    padding-bottom: 44px;
  }

  @media (max-width: $mobile-width) {
    font-size: 32px;
    line-height: 32px;
    padding-bottom: 45px;
  }
}

.stocks__wrapper {
  display: flex;
  justify-content: center;
  min-height: 462px;
  padding-left: 10px;
  padding-right: 10px;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    padding-left: 45px;
    padding-right: 45px;
    min-width: 678px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 15px;
    padding-right: 15px;
    min-width: 320px;
    max-width: 320px;
    margin: 0 auto;
  }
}

.stocks__subscription {
  display: flex;
  max-width: 560px;
  max-height: 462px;
  margin-right: 40px;
  flex-basis: 50%;
  background: #ed0233;
  background-image: url("../img/trained-woman.jpg");
  background-repeat: no-repeat;
  background-position-x: right;

  @media (max-width: $tablet-width) {
    margin-right: auto;
    margin-left: auto;
    min-width: 678px;
    flex-basis: auto;
    margin-bottom: 30px;
    background-image: url("../img/trained-woman-middle.jpg");
  }

  @media (max-width: $mobile-width) {
    min-height: 598px;
    flex-direction: column;
    min-width: 100%;
    max-width: 100%;
    width: 290px;
    background-image: url("../img/trained-woman-small.jpg");
    background-position-y: bottom;
  }
}

.stocks__subscription_info {
  display: flex;
  flex-direction: column;
  padding-left: 45px;
  padding-right: 5px;
  flex-basis: 50%;
  max-height: 462px;
  padding-bottom: 46px;
  padding-top: 32px;

  @media (max-width: $tablet-width) {
    width: 279px;
    height: 462px;
    flex-basis: auto;
    padding-right: 34px;
    max-height: 100%;
    padding-bottom: 46px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 0;
    padding-right: 0;
    text-align: center;
    align-items: center;
    width: 290px;
    height: 299px;
    margin: 0 auto;
  }
}

.stocks__subscription_info h3 {
  font-family: $textRoboto;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 156%;
  /* or 44px */
  color: #ffffff;
  margin-bottom: 8px;
}

.stocks__subscription span {
  font-family: $textRoboto;
  font-style: normal;
  font-weight: bold;
  font-size: 70px;
  line-height: 47px;
  color: #ffffff;
  margin-bottom: 31px;

  @media (max-width: $mobile-width) {
    margin-bottom: 25px;
  }
}

.stocks__subscription_info p {
  font-family: $textRoboto;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 140%;
  /* or 25px */
  color: #ffffff;
  padding-right: 48px;
  margin-bottom: auto;

  @media (max-width: $tablet-width) {
    letter-spacing: 0.0155em;
    padding-right: 0;
  }

  @media (max-width: $mobile-width) {
    letter-spacing: 0.0155em;
    padding-left: 45px;
    padding-right: 45px;
  }
}

.stocks__subscription_info-detail {
  width: 80px;
  font-family: $textRoboto;
  min-width: 80px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border-bottom: 1px solid #ffffff;

  &:hover {
    opacity: 0.5;
  }
}

.stocks__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  max-width: 560px;
  flex-basis: 50%;

  @media (max-width: $tablet-width) {
    flex-direction: row;
    margin: 0 auto;
    max-width: 678px;
    min-width: 678px;
    width: 678px;
    min-height: 211px;
    flex-wrap: wrap;
    flex-basis: auto;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    min-height: auto;
    max-width: 290px;
    min-width: 290px;
    width: 290px;
  }
}

.stocks__list_card {
  display: flex;
  flex-direction: column;
  min-height: 211px;
  background-image: url("../img/border-stocks.png");
  background-size: 100% 100%;
  padding-left: 35px;
  padding-right: 65px;
  margin-bottom: 40px;
  padding-top: 35px;
  padding-bottom: 36px;

  @media (max-width: $tablet-width) {
    background-image: url("../img/border-stocks-middle.png");
    min-height: 211px;
    width: 324px;
    margin-right: 30px;
    max-width: 324px;
    min-width: 324px;
  }

  @media (max-width: $mobile-width) {
    background-image: url("../img/border-stocks-small.png");
    text-align: center;
    padding: 0 40px;
    padding-top: 45px;
    padding-bottom: 47px;
    margin-bottom: 30px;
    margin-right: 0;
    width: 290px;
    max-width: 290px;
    min-width: 290px;
  }

  &:last-child {
    margin-bottom: 0;

    @media (max-width: $tablet-width) {
      margin-bottom: 40px;
    }

    @media (max-width: $mobile-width) {
      margin-bottom: 30px;
    }
  }

  &:nth-child(2n) {
    @media (max-width: $tablet-width) {
      margin-right: 0;
    }
  }
}

.stocks__list_card h3 {
  font-family: $textRoboto;
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 18px;
  color: #1c3374;
  letter-spacing: 0.03em;
  margin-bottom: 16px;

  @media (max-width: $tablet-width) {
    margin-bottom: 14px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 16px;
  }
}

.stocks__list_card p {
  font-family: $textRoboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  /* or 22px */
  color: #1c3374;
  letter-spacing: 0.015em;
  margin-bottom: auto;
}

.stocks__list_info-detail {
  width: 69px;
  font-family: $textRoboto;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1c3374;
  border-bottom: 1px solid #1c3374;

  @media (max-width: $mobile-width) {
    margin: 0 auto;
    text-align: center;
  }

  &:hover {
    opacity: 0.5;
  }
}
